/* ***************** CAVITY_BALANCE_GRID_CSS ******************** */

.Cavity-Grid-Container{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}

.Cavity-Grid-1-Container{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1000%;
}

.Cavity-Grid-2-Container tr th{
  width: 20%;
}

.table-heading-content{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-heading-content-editable{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  border-radius: 2px;
  padding: 2px 5px;
}

.table-heading-icons{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 20%;
  font-size: 0.95rem;
  cursor: pointer;
  margin-bottom: 5px;
}

@media (min-height: 768px) and (max-height:1024px){
  .table-heading-icons{
    font-size: 0.7rem;
    width: 20%;
  }

  .table-heading-editable input{
    width: 60%;
    margin: 3px 2px;
  }

  .table-heading{
    width: 80%;
  }

}

.table-heading{
  width: 70%;
}

.table-heading-editable{
  width: 60%;
}

.table-heading input{
  width: 40%;
}
.table-heading-editable input{
  border: none;
  outline: 0;
}

/* ******************* VISCOCITY_GRID_CSS ******************* */
.viscocity_icons{
  font-size: 15px;
  margin-left: 20px;
  cursor: pointer;
}

.viscosity_table{
  height: 280px;
  overflow: scroll;
}

.viscosity_heading{
  width: 150px;
}

.viscosity_heading2{
  width: 170px;
}

/* ****************** Pressure Drop ********************** */
.Pressure_Heading{
  width: 300px;
}

/* *********************** Cold Runner *************** */
.Cold-Grid-Container{
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.cold_table{
  height: 350px;
  overflow-x: scroll;
  width: 1000px;
}

.cold_table2{
  height: 450px;
  overflow-x: scroll;
}

.cold-runner-grid{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.cold-grid{
  width: 35%;
}

.cold-chart{
  width: 62%;
}

@media (min-height: 768px) and (max-height:1024px){
  .cold-runner-grid{
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
    margin: 0;
  }

  .cold-grid{
    width: 60%;
    margin-bottom: 15px;
  }

  .cold-chart{
    width: 100%;
  }

  .cold-heading{
    width: 200px;
  }

  .cold_table2{
    height: 250px;
    overflow-x: scroll;
  }

  .cold_table{
    height: 250px;
    overflow-x: scroll;
    width: 700px;
  }
}

