.ribbon-wrapper, .ribbon-wrapper-right, .ribbon-wrapper-bottom, .ribbon-wrapper-right-bottom {
  position: relative;
  padding-top: 40px; }

.ribbon-vertical-left-wrapper {
  padding-left: 40px;
  position: relative; }

.ribbon-overflow {
  overflow: hidden; }

.ribbon-vertical-right-wrapper {
  padding-right: 40px; }

.ribbon-wrapper-bottom {
  padding-bottom: 40px;
  padding-top: 0; }

.ribbon-wrapper-right-bottom {
  padding-right: 40px; }

.ribbon-content {
  margin-bottom: 0; }

.ribbon {
  padding: 0 20px;
  height: 30px;
  line-height: 30px;
  clear: left;
  position: absolute;
  top: 12px;
  left: 0px;
  color: #fff !important;
  z-index: 1; }

.ribbon-space-bottom {
  top: 0px; }

.ribbon-clip {
  left: -14px; }
  .ribbon-clip:before {
    position: absolute;
    top: 30px;
    left: 0;
    width: 0;
    height: 0;
    content: "";
    border: 7px solid transparent;
    border-top-color: #2a3142;
    border-right-color: #2a3142; }

.ribbon-bookmark:before {
  position: absolute;
  top: 0;
  left: 100%;
  display: block;
  width: 0;
  height: 0;
  content: '';
  border: 15px solid #2a3142;
  border-right: 10px solid transparent; }

.ribbon-right {
  left: auto;
  right: -2px; }

.ribbon-clip-right {
  right: -14px; }
  .ribbon-clip-right:before {
    position: absolute;
    top: 30px;
    right: 0;
    width: 0;
    height: 0;
    content: "";
    border: 7px solid transparent;
    border-top-color: #2a3142;
    border-right-color: #2a3142; }

.ribbon-clip-bottom {
  left: -14px;
  bottom: 12px;
  top: auto; }
  .ribbon-clip-bottom:before {
    position: absolute;
    bottom: 30px;
    left: 0;
    width: 0;
    height: 0;
    content: "";
    border: 7px solid transparent;
    border-top-color: #2a3142;
    border-right-color: #2a3142; }

.ribbon-clip-bottom-right {
  right: -14px;
  left: auto;
  bottom: 12px;
  top: auto; }
  .ribbon-clip-bottom-right:before {
    position: absolute;
    bottom: 30px;
    right: 0;
    width: 0;
    height: 0;
    content: "";
    border: 7px solid transparent;
    border-top-color: #2a3142;
    border-right-color: #2a3142; }

.ribbon-bookmark.ribbon-right:before {
